<template>
    <div>
        <h3></h3>
        <div class="add">
            <form @submit="onSubmit">
                <input type="text" v-model="title" placeholder="해야할 일">
                <input type="submit" value="작성">
            </form>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    name: "AddTodo",
    data() {
        return {
            title: ""
        }
    },
    methods: {
        ...mapActions(['addTodo']),
        onSubmit(e) {
            e.preventDefault();
            this.addTodo(this.title)
            this.title = "";
        }
    }
}
</script>

