<template>
    <div>
        <div class="legend">
            <span>완료한 일은 더블클릭하세요.</span>
            <span>
                <span class="incomplete-box"></span> = 해야할 일
            </span>
            <span>
                <span class="complete-box"></span> = 완료한 일
            </span>
        </div>
        <div class="todos">
            <div 
                @dblclick="onDblClick(todo)" 
                v-for="todo in allTodos" 
                :key="todo.id" 
                class="todo"
                v-bind:class="{'is-complete':todo.completed}"
            >
                <p>{{ todo.title }}</p>
                <i @click="deleteTodo(todo.id)" class="fas fa-trash-alt"/>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    // eslint-disable-next-line
    name: "Todos",
    methods: {
        ...mapActions(['fetchTodos', 'deleteTodo', 'updateTodo']),
        onDblClick(todo) {
            const updTodo = {
                id : todo.id,
                title : todo.title,
                completed : !todo.completed
            }
            this.updateTodo(updTodo)
        }
    },
    computed: mapGetters(['allTodos']),
    created() {
        this.fetchTodos();
    }

}
</script>

<style scoped>
    .todos {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 2rem;
    }
    .todo {
        border: 1px solid #ccc;
        background: #f7ff9e;
        padding: 1rem;
        margin: auto;
        width: 80%;
        line-height: 7rem;
        height: 7rem;
        text-align: center;
        position: relative;
        cursor: pointer;
    }
    .todo p {
        line-height: 1.5;
        display: inline-block;
        vertical-align: middle;
    }
    i {
        position: absolute;
        bottom: 10px;
        right: 10px;
        cursor: pointer;
    }

    .legend {
        display: flex;
        justify-content: space-around;
        margin-bottom: 1rem;
        color: white;
    }
    .complete-box {
        display: inline-block;
        width: 10px;
        height: 10px;
        border: #ccc 1px solid;
        background: rgb(22, 76, 58);
    }
    .incomplete-box {
        display: inline-block;
        width: 10px;
        height: 10px;
        background: #f7ff9e;
    }
    .is-complete {
        background: rgb(22, 76, 58);
        color: white;
    }

    @media (max-width: 500px) {
        .todos {
            grid-template-columns: 1fr;
        }
    }
</style>